.recommended {
  &__products {
    @include page-grid;

    row-gap: 40px;
  }

  &__product {
    grid-column: 1/-1;
    transition: transform $effectDuration;

    &:hover {
      transform: scale(1.05);
    }

    @include on-tablet {
      grid-column: 2/-2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
  }
}
